.timeline{z-index:100;position:absolute;top:9rem;left:6rem;height:calc(100vh - 9rem - 5rem);display:flex;flex-direction:column;transition:left .5s ease}.timeline::before{content:"";position:absolute;width:.5rem;height:100vh;top:-9rem;right:0;background:#319fff}.timeline.empty::before{right:-4rem}.timeline .pastTrips{margin-top:1rem;margin-left:-20rem;padding-right:15rem;padding-bottom:3rem;padding-left:5rem;transform:translateX(15rem);position:relative;flex:auto;overflow-y:auto;scrollbar-width:none;mask-image:linear-gradient(to bottom, transparent 0%, black 1rem, black calc(100% - 4rem), transparent 100%)}.timeline .pastTrips::-webkit-scrollbar{display:none}.timeline .pastTrips .tripCheckpoint{position:relative;padding:.7rem 0 .7rem;max-width:33rem;margin-left:-3rem;border-right:.5rem solid #319fff;padding-right:4rem;overflow:visible}.timeline .pastTrips .tripCheckpoint::before{content:"";background:#319fff;opacity:.25;width:4rem;height:.2rem;position:absolute;top:50%;transform:translateY(-50%);right:0;z-index:-1}.timeline .pastTrips .tripCheckpoint .dot::before{content:"";background:#319fff;width:1.5rem;aspect-ratio:1;border-radius:50%;position:absolute;top:50%;transform:translateY(-50%);right:-1rem}.timeline .pastTrips .tripCheckpoint .tripCardContainer{position:relative;padding-left:3rem}.timeline .pastTrips .tripCheckpoint .tripCardContainer .trashContainer{display:none;position:absolute;top:50%;transform:translateY(-50%);left:-0.5rem;white-space:nowrap;border-radius:50%;padding:.5rem;cursor:pointer;transition:.3s}.timeline .pastTrips .tripCheckpoint .tripCardContainer .trashContainer:hover{background-color:#eaf5ff}.timeline .pastTrips .tripCheckpoint .tripCardContainer .trashContainer .trash{height:2rem}.timeline .pastTrips .tripCheckpoint .tripCardContainer:hover .trashContainer{display:block}.timeline .pastTrips .tripCheckpoint .year,.timeline .pastTrips .tripCheckpoint .dates{position:absolute;left:calc(100% + 2rem);font-weight:300;font-style:italic;color:#24292f;white-space:nowrap;width:fit-content}.timeline .pastTrips .tripCheckpoint .year{top:10%;font-size:2rem}.timeline .pastTrips .tripCheckpoint .dates{top:50%;transform:translateY(-50%);font-size:1.4rem}